.search {
  text-align: center;
}

.search__title {
  @include text-size(21px, 26px);

  @media (min-width: $tablet-width) {
    @include text-size(30px, 26px);
  }
}

.search__text {
  @include text-size(14px, 24px);
  color: $light_black;
}

.search__button {
  @media (min-width: $tablet-width) {
    margin: 0 auto;
    width: 458px;
  }
}
